:root {
  --bs-blue: #007bff;
  --bs-indigo: #6610f2;
  --bs-purple: #6f42c1;
  --bs-pink: #e83e8c;
  --bs-red: #dc3545;
  --bs-orange: #EC6F50;
  --bs-yellow: #ffc107;
  --bs-green: #00E57E;
  --bs-teal: #20c997;
  --bs-cyan: #17a2b8;
  --bs-white: #fff;
  --bs-gray: #6c757d;
  --bs-gray-dark: #343a40;
  --bs-primary: #00E57E;
  --bs-secondary: #6c757d;
  --bs-success: #00E57E;
  --bs-info: #17a2b8;
  --bs-warning: #ffc107;
  --bs-danger: #dc3545;
  --bs-light: #f8f9fa;
  --bs-dark: #212529;
  --bs-font-sans-serif: lato, times, sans-serif, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  --bs-font-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  --bs-gradient: linear-gradient(180deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0)); }

*,
*::before,
*::after {
  box-sizing: border-box; }

@media (prefers-reduced-motion: no-preference) {
  :root {
    scroll-behavior: smooth; } }

body {
  margin: 0;
  font-family: var(--bs-font-sans-serif);
  font-size: 1.1875rem;
  font-weight: 400;
  line-height: 1.5;
  color: #040504;
  background-color: #fafafa;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0 !important; }

hr {
  margin: 1rem 0;
  color: inherit;
  background-color: currentColor;
  border: 0;
  opacity: 0.25; }

hr:not([size]) {
  height: 1px; }

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  margin-top: 0;
  margin-bottom: 0.5rem;
  font-family: lato;
  font-weight: 700;
  line-height: 1.2; }

h1, .h1 {
  font-size: calc(1.50025rem + 3.003vw); }
  @media (min-width: 1200px) {
    h1, .h1 {
      font-size: 3.7525rem; } }

h2, .h2 {
  font-size: calc(1.33281rem + 0.99375vw); }
  @media (min-width: 1200px) {
    h2, .h2 {
      font-size: 2.07812rem; } }

h3, .h3 {
  font-size: calc(1.30313rem + 0.6375vw); }
  @media (min-width: 1200px) {
    h3, .h3 {
      font-size: 1.78125rem; } }

h4, .h4 {
  font-size: calc(1.27344rem + 0.28125vw); }
  @media (min-width: 1200px) {
    h4, .h4 {
      font-size: 1.48438rem; } }

h5, .h5 {
  font-size: calc(1.27344rem + 0.28125vw); }
  @media (min-width: 1200px) {
    h5, .h5 {
      font-size: 1.48438rem; } }

h6, .h6 {
  font-size: 1.1875rem; }

p {
  margin-top: 0;
  margin-bottom: 1rem; }

abbr[title],
abbr[data-bs-original-title] {
  text-decoration: underline;
  text-decoration: underline dotted;
