#doc{
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	animation: fadein 1s;
	overflow: hidden;
}
@keyframes fadein {

	from {
		opacity: 0;
	}

	to {
		opacity: 1;
	}

}
